import { request, requestDownload } from 'utils';

const prefix = '/api/students';

export const getStudentField = () =>
  request({
    prefix,
    url: `/Student/GetStudentField`,
    method: 'GET',
    params: {
    },
    data: {},
  });

export const getStudentWithFields = (params) =>
  request({
    prefix,
    url: `/Student/GetStudentWithFields`,
    method: 'POST',
    data: params,
    params: {},
  });

export const exportStudentWithFields = (params) =>
    requestDownload({
      defaultFileName: 'File_ExportStudentsByFields.xlsx',
      prefix,
      url: `/Student/ExportStudentWithFields`,
      method: 'POST',
      data: params,
      params: {},
    });


